"use client"

import Link from 'next/link';
import Image from 'next/image';

const ProjectCard = ({ project, isFeatured = false }) => {
  const { title, slug, description, category, images } = project;
  const imageSrc = images[0] || '/images/placeholder.jpg';

  return (
    <article className={`bg-white rounded-xl overflow-hidden shadow-xl hover:shadow-2xl transition duration-300 ${isFeatured ? 'col-span-1' : ''}`}>
      <div className="relative h-64 overflow-hidden">
        <Image 
          src={imageSrc} 
          alt={title} 
          fill 
          sizes="(max-width: 768px) 100vw, 33vw"
          className="object-cover transition-transform duration-500 hover:scale-[1.03]" 
        />
        <span className="absolute top-4 left-4 bg-[#4D4DFF] text-white text-xs font-semibold px-3 py-1 rounded-full">
          {category}
        </span>
      </div>
      
      <div className="p-6">
        <h3 className="text-2xl font-bold text-[#1A1A1A] mb-2">{title}</h3>
        <p className="text-[#666666] mb-4 line-clamp-3">{description}</p>
        
        <Link 
          href={`/our-work/${slug}`}
          className={`font-semibold py-2 px-4 rounded-full transition duration-300 ${
            isFeatured 
              ? 'border-2 border-[#4D4DFF] text-[#4D4DFF] hover:bg-[#4D4DFF] hover:text-white' 
              : 'text-[#FF4D4D] hover:underline'
          }`}
        >
          View {isFeatured ? 'Project' : 'Details'} &rarr;
        </Link>
      </div>
    </article>
  );
};

export default ProjectCard;